html {
  font-size: 16px;
  box-sizing: border-box;
  height: 100%;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

:root {
  --bg-color: hsl(140, 100%, 94%);
  --font-color: hsl(140, 10%, 10%);
  --white: hsl(0, 100%, 100%);
  --border-color: #ccc;
  --note-bg: hsl(120, 100%, 97%);
  --scrollbar-track-bg: hsl(140, 100%, 94%);
  --scrollbar-thumb-bg: hsl(140, 100%, 45%);
  --delete-btn-color: hsl(140, 100%, 45%);
  --delete-btn-hover: hsl(140, 100%, 30%);
  --heebo: "Heebo", sans-serif;
  --rocker-font: "New Rocker", sans-serif;
}

body {
  font: 400 100%/160% var(--heebo);
  background-color: var(--bg-color);
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 2rem 1rem;
}

/* ***********************
* Theme Switcher Styling
************************ */
.theme--color--switcher {
  position: absolute;
  top: 2rem;
  right: 1rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
}
.theme--color--switcher p {
  font-weight: 500;
  text-transform: uppercase;
  color: var(--font-color);
  letter-spacing: 0.75px;
}
.theme--color--switcher div {
  width: 65px;
  height: 34px;
  background-color: var(--font-color);
  border-radius: 30px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.2);
  position: relative;
  transition: background-color 300ms ease;
}
.theme--color--switcher div span {
  position: absolute;
  top: 4px;
  left: 4px;
  background-color: var(--bg-color);
  height: 26px;
  width: 26px;
  border-radius: 50%;
  transition: background-color 300ms ease;
}

/* ***********************
* Notes Main Container Styling
*********************** */
.notes--main {
  background-color: var(--white);
  border-radius: 4px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.07), 10px 10px 25px rgba(0, 0, 0, 0.075), -5px -5px 10px rgba(0, 0, 0, 0.07), -10px -10px 25px rgba(0, 0, 0, 0.075);
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
}
.notes--main h1 {
  text-align: center;
  font-family: var(--rocker-font);
  padding: 1rem;
  text-transform: uppercase;
  border-bottom: 1px solid var(--border-color);
}
.notes--main form {
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid var(--border-color);
}

.notes__container {
  padding: 1rem;
  display: grid;
  max-height: 350px;
  grid-template-columns: 1fr;
  grid-gap: 15px 0;
  overflow-y: scroll;
}
.notes__container::-webkit-scrollbar {
  width: 13px;
}
.notes__container::-webkit-scrollbar-track {
  background-color: var(--scrollbar-track-bg);
  box-shadow: 3px 0 5px rgba(0, 0, 0, 0.1) inset;
}
.notes__container::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background-color: var(--scrollbar-thumb-bg);
}

.note {
  background-color: var(--note-bg);
  padding: 0.5rem 1rem;
  box-shadow: 3px 3px 4px rgba(0, 0, 0, 0.07);
  transition: transform 400ms ease, box-shadow 400ms ease;
}
.note:hover {
  transform: translateY(-4px);
  box-shadow: 4px 4px 5px rgba(0, 0, 0, 0.2);
}
.note__content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.note__content p {
  font-size: 1.1rem;
  font-weight: 500;
  color: var(--font-color);
}
.note__content button {
  background-color: transparent;
  padding: 0;
  border: none;
}
.note__content button span {
  color: var(--delete-btn-color);
}
.note__content button:hover span, .note__content button:focus span {
  color: var(--delete-btn-hover);
  cursor: pointer;
}
.note h5 {
  font-weight: 500;
  font-size: 0.7rem;
  color: var(--font-color);
}

/* **************************
 * MEDIA QUERIES
 * *************************/
@media screen and (max-width: 480px) {
  body {
    font-size: 90%;
    align-items: flex-start;
    padding-top: 5rem;
  }
}
@media screen and (min-width: 992px) {
  body {
    font-size: 110%;
  }
}